.MemoryCardBack {
    background: repeating-linear-gradient(
    45deg,
    #606dbc,
    #606dbc 10px,
    #465298 10px,
    #465298 20px
  );
  }
  
  img {
    width: 100%;
    padding-top: 20px;
  }
  
  .MemoryCard {
    display: inline-block;
    width: 150px;
    height: 80px;
    margin: 5px;
    cursor: pointer;
    position: relative;
    perspective: 800px;
  }
  
  .MemoryCardInner {
    width: 100%;
    height: 100%;
    position: absolute;
    transform-style: preserve-3d;
    transition: transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    border-radius: 5px;
    border: 3px solid navy;
    box-shadow: 0 6px 16px rgba(0,0,0,0.15);
  }
  
  .MemoryCardBack,
  .MemoryCardFront {
      position: absolute;
      width: 100%;
      height: 100%;
      backface-visibility: hidden;
  }
  
  .MemoryCardFront {
    font-size: 30px;
    line-height: 20px;
    transform: rotateY(180deg);
  }
  
  .MemoryCardInner.flipped {
    transform: rotateY(180deg);
  }

